@import "./_variables";
@import "./_mixins";

body {
    min-height: calc(var(--vh, 1vh) * 100);
    overflow-x: hidden;

    &.-isPc {
        background: #ade2b0 url(../image/banner/pc.jpg) no-repeat center/contain;

        &>div:not(.-showPc) {
            display: none;
        }
    }

    &.-isPopupOpen,
    &.-menuOpen {
        overflow: hidden;
    }
}

.container {
    position: relative;
    box-sizing: border-box;
    max-width: 1170px;
    margin: 0 auto;
    background: url(../image/bg/bg.jpg) repeat-y top center/100%;
    overflow: hidden;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        width: 562px;
        height: 251px;
        background: url(../image/bg/cloud.png) no-repeat center/contain;
        pointer-events: none;
    }

    &.-index {
        background-image: url(../image/bg/index.jpg);
    }

    &.-intro {
        background-image: url(../image/bg/index.jpg);

        &::before {
            content: none;
        }
    }

    @include breakpoint-down('tablet') {
        &::before {
            width: vw(562);
            height: vw(251);
        }
    }
}

.landscape {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #fff;
    background-color: rgba(0, 0, 0, .8);
    backdrop-filter: blur(15px);
    z-index: 1000;

    @media (orientation: landscape) {
        body:not(.-isPc) & {
            display: flex;
        }
    }
}

@keyframes lds-ellipsis1 {
    0% {
        transform: scale(0);
    }

    100% {
        transform: scale(1);
    }
}

@keyframes lds-ellipsis2 {
    0% {
        transform: translate(0, 0);
    }

    100% {
        transform: translate(24px, 0);
    }
}

@keyframes lds-ellipsis3 {
    0% {
        transform: scale(1);
    }

    100% {
        transform: scale(0);
    }
}

.loading {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: 9999;

    &-wrap {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: inline-block;
        width: 80px;
        height: 80px;
    }

    &-dot {
        position: absolute;
        top: 33px;
        width: 13px;
        height: 13px;
        border-radius: 50%;
        background: #93bc58;

        &:nth-child(1) {
            left: 8px;
            animation: .6s infinite lds-ellipsis1;
        }

        &:nth-child(2) {
            left: 32px;
            animation: .6s infinite lds-ellipsis2;
        }

        &:nth-child(3) {
            left: 56px;
            animation: .6s infinite lds-ellipsis3;
        }
    }
}

/* ------------------------------- Header ------------------------------- */
.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 99;

    &-wrap {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: space-between;
        max-width: 1170px;
        margin: 0 auto;
        padding: 24px 48px;
    }

    &-logo {
        flex: 0 0 auto;
        width: 216px;
        font-size: 0;

        img {
            width: 100%;
        }

        .-card & {
            opacity: 0;
            pointer-events: none;
        }
    }

    &-burger {
        flex: 0 0 auto;
        width: 144px;
        height: 144px;
        font-size: 0;
        background: url(../image/layout/menu.svg) no-repeat center/contain;
        box-shadow: 16px 15px 21px 0 rgba(4, 54, 1, 0.16);
    }

    &-nav {
        position: fixed;
        top: 0;
        left: 0;
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        padding: 188px 0 124px;
        background-color: rgba(0, 0, 0, 0.5);
        opacity: 0;
        pointer-events: none;
        transition: opacity .3s;

        & * {
            pointer-events: none;
        }

        .-menuOpen & {
            opacity: 1;
            pointer-events: visible;

            & * {
                pointer-events: visible;
            }
        }
    }

    @include breakpoint-down('tablet') {
        &-wrap {
            padding: vw(24) vw(48);
        }

        &-logo {
            width: vw(216);
        }

        &-burger {
            width: vw(144);
            height: vw(144);
            box-shadow: vw(16) vw(15) vw(21) 0 rgba(4, 54, 1, 0.16);
        }

        &-nav {
            padding: vw(188) 0 vw(124);
        }
    }
}

.nav {
    position: relative;
    width: 980px;
    margin: 0 auto;
    font-size: 61px;
    font-weight: 600;
    line-height: 1;
    text-align: center;

    &-user {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 164px;
        border: 8px solid #fff;
        border-radius: 50%;
        overflow: hidden;
        z-index: 1;

        img {
            width: 100%;
        }
    }

    &-content {
        padding-top: 92px;

        &>.board .board-scroll {
            max-height: calc((var(--vh, 1vh) * 100) - (188px + 124px + 92px + 95px + 95px));
        }
    }

    &-name {
        padding-top: 28px;
    }

    &-links {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 96px;

        a {
            display: flex;
            align-items: center;
            gap: 20px;
            font-weight: 600;
            color: #483629;
            text-decoration: none;

            &::before,
            &::after {
                content: '';
                flex: 0 0 auto;
                width: 43px;
                height: 23px;
                margin-top: 6px;
                background: url(../image/icon/leaf.svg) no-repeat center/contain;
            }

            &::after {
                transform: rotate(180deg);
            }
        }
    }

    &-social {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 80px;
        margin-top: 106px;

        a {
            flex: 0 0 auto;
            width: 105px;

            img {
                width: 100%;
            }
        }
    }

    &-logout {
        margin-top: 70px;
    }

    @include breakpoint-down('tablet') {
        width: vw(980);
        font-size: vw(61);

        &-user {
            width: vw(164);
            border-width: vw(8);
        }

        &-content {
            padding-top: vw(92);

            &>.board .board-scroll {
                max-height: calc((var(--vh, 1vh) * 100) - #{vw(594)});
            }
        }

        &-name {
            padding-top: vw(28);
        }

        &-links {
            gap: vw(96);

            a {
                gap: vw(20);

                &::before,
                &::after {
                    width: vw(43);
                    height: vw(23);
                    margin-top: vw(6);
                }
            }
        }

        &-social {
            gap: vw(80);
            margin-top: vw(106);

            a {
                width: vw(105);
            }
        }

        &-logout {
            margin-top: vw(70);
        }
    }
}

/* ------------------------------- Main ------------------------------- */
.main {
    min-height: calc(var(--vh, 1vh) * 100);
}

