.action {
    display: flex;
    justify-content: center;

    @include breakpoint-down('mobile') {}
}

.btn {
    position: relative;
    box-sizing: border-box;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 25px;
    width: 765px;
    padding: 40px 40px 41px;
    font-size: 60px;
    font-weight: 600;
    line-height: 1.15;
    text-decoration: none;
    border-radius: 75px;
    box-shadow: 16px 15px 21px 0 rgba(4, 54, 1, 0.16);
    background: #fcfdfc url(../image/bg/pattern.jpg) repeat center/130px;

    &::before {
        content: '';
        position: absolute;
        top: 10px;
        right: 13px;
        bottom: 10px;
        left: 13px;
        border: 2px solid #483629;
        border-radius: 65px;
        pointer-events: none;
    }

    span {
        flex: 0 0 auto;
    }

    &::after {
        flex: 0 0 auto;
        content: '';
        width: 20px;
        height: 36px;
        margin-top: 6px;
        background: url(../image/icon/arrow.svg) no-repeat center/contain;
    }

    &.-transparent {
        box-shadow: none;
        background: none;
    }

    &.-full {
        width: 100%;
    }

    &.-index {
        width: 977px;
        justify-content: flex-end;
        padding: 56px 320px 55px 56px;
        border-radius: 90px;

        &::before {
            border-radius: 80px;
        }

        &>img {
            position: absolute;
            top: -45px;
            right: 110px;
            width: 195px;
        }
    }

    &:disabled,
    &.-disabled {
        pointer-events: none;

        span {
            &::after {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                border-radius: 90px;
                background-color: rgba(141, 141, 141, 0.5);
                mix-blend-mode: multiply;
            }
        }
    }

    @include breakpoint-down('tablet') {
        gap: vw(25);
        width: vw(765);
        padding: vw(40) vw(40) vw(41);
        font-size: vw(60);
        border-radius: vw(75);
        box-shadow: vw(16) vw(15) vw(21) 0 rgba(4, 54, 1, 0.16);
        background-size: vw(130);

        &::before {
            top: vw(10);
            right: vw(13);
            bottom: vw(10);
            left: vw(13);
            border-width: 1px;
            border-radius: vw(65);
        }

        &::after {
            width: vw(20);
            height: vw(36);
            margin-top: vw(6);
        }

        &.-index {
            width: vw(977);
            padding: vw(56) vw(320) vw(55) vw(56);
            border-radius: vw(90);

            &::before {
                border-radius: vw(80);
            }

            &>img {
                top: vw(-45);
                right: vw(110);
                width: vw(195);
            }
        }

        &:disabled,
        &.-disabled {
            span {
                &::after {
                    border-radius: vw(90);
                }
            }
        }
    }
}

.socialBtn {
    box-sizing: border-box;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 615px;
    height: 125px;
    font-size: 48px;
    font-weight: 600;
    color: #FFF;
    text-decoration: none;
    letter-spacing: 1px;
    border-radius: 63px;

    &.-fb {
        background-color: #5577d4;
    }

    &.-line {
        background-color: #2bd65e;
    }

    // &.-download {
    //     background-color: #deb146;
    // }

    @include breakpoint-down('tablet') {
        width: vw(615);
        height: vw(125);
        font-size: vw(48);
        border-radius: vw(63);
    }
}

.link {
    display: inline-flex;
    align-items: center;
    gap: 20px;
    font-size: 48px;
    font-weight: 600;
    line-height: 1.5;
    letter-spacing: 1px;
    color: #63a378;
    text-decoration: none;

    img {
        flex: 0 0 auto;
        width: 96px;
    }

    &:disabled {
        opacity: 0.5;
        pointer-events: none;
    }

    @include breakpoint-down('tablet') {
        gap: vw(20);
        font-size: vw(48);
        letter-spacing: vw(1);

        img {
            width: vw(96);
        }
    }
}