$boardStart: 77px;
$boardEnd: calc(100% - $boardStart);

$lineStart: 74px;
$lineEnd: calc(100% - $lineStart);
$lineW: 3px;

.board {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 980px;
    margin: 0 auto;

    &::before {
        content: '';
        position: absolute;
        top: 16px;
        left: 15px;
        width: 100%;
        height: 100%;
        background:
            linear-gradient(45deg, transparent 54px - 10px, rgba(4, 54, 1, 0.16) 54px)bottom left,
            linear-gradient(-45deg, transparent 54px - 10px, rgba(4, 54, 1, 0.16) 54px)bottom right,
            linear-gradient(-135deg, transparent 54px - 10px, rgba(4, 54, 1, 0.16) 54px)top right,
            linear-gradient(135deg, transparent 54px - 10px, rgba(4, 54, 1, 0.16) 54px)top left;
        background-size: 51% 51%;
        background-repeat: no-repeat;
        filter: blur(10px);
    }

    &-wrap {
        flex: 0 1 auto;
        box-sizing: border-box;
        min-height: 200px;
        padding: 95px 35px;
        background: #fcfdfc url(../image/bg/pattern.jpg) repeat left center/150px;
        clip-path:
            polygon($boardStart 0,
                $boardEnd 0,
                100% $boardStart,
                100% $boardEnd,
                $boardEnd 100%,
                $boardStart 100%,
                0 $boardEnd,
                0 $boardStart);
    }

    &::after {
        content: '';
        position: absolute;
        top: 18px;
        right: 18px;
        bottom: 18px;
        left: 18px;
        background-color: #483629;
        clip-path: polygon($lineStart 0,
                $lineEnd 0,
                100% $lineStart,
                100% $lineEnd,
                $lineEnd 100%,
                $lineStart 100%,
                0 $lineEnd,
                0 $lineStart,
                $lineStart 0,

                calc($lineStart + $lineW/2) $lineW,
                $lineW calc($lineStart + $lineW/2),
                $lineW calc($lineEnd - $lineW/2),
                calc($lineStart + $lineW/2) calc(100% - $lineW),
                calc($lineEnd - $lineW/2) calc(100% - $lineW),
                calc(100% - $lineW) calc($lineEnd - $lineW/2),
                calc(100% - $lineW) calc($lineStart + $lineW/2),
                calc($lineEnd - $lineW/2) $lineW,
                calc($lineStart + $lineW/2) $lineW);
        pointer-events: none;
    }

    &-head {
        padding: 0 25px;
        text-align: center;

        &>span {
            font-size: 72px;
            font-weight: 600;
            line-height: 1;
            letter-spacing: 1.5px;
        }
    }

    &-content {
        padding: 0 25px;

        &::before {
            content: '';
            display: block;
            margin: 60px auto;
            width: 674px;
            height: 2px;
            background-color: #483629;
        }
    }

    &-close {
        position: absolute;
        top: 50px;
        right: 57px;
        width: 120px;
        height: 120px;
        background: url(../image/icon/cross.svg) no-repeat center/contain;
    }

    @include breakpoint-down('tablet') {
        width: vw(980);

        &::before {
            top: vw(16);
            left: vw(15);
            background: linear-gradient(45deg, transparent #{vw(44)}, rgba(4, 54, 1, 0.16) #{vw(54)})bottom left,
            linear-gradient(-45deg, transparent #{vw(44)}, rgba(4, 54, 1, 0.16) #{vw(54)})bottom right,
            linear-gradient(-135deg, transparent #{vw(44)}, rgba(4, 54, 1, 0.16) #{vw(54)})top right,
            linear-gradient(135deg, transparent #{vw(44)}, rgba(4, 54, 1, 0.16) #{vw(54)})top left;
            background-size: 51% 51%;
            background-repeat: no-repeat;
            filter: blur(#{vw(10)});
        }

        $boardStart: vw(77);
        $boardEnd: calc(100% - $boardStart);

        $lineStart: vw(74);
        $lineEnd: calc(100% - $lineStart);
        $lineW: 1.5px;

        &-wrap {
            min-height: vw(200);
            padding: vw(95) vw(35);
            background-size: vw(150);
            clip-path:
                polygon($boardStart 0,
                    $boardEnd 0,
                    100% $boardStart,
                    100% $boardEnd,
                    $boardEnd 100%,
                    $boardStart 100%,
                    0 $boardEnd,
                    0 $boardStart);
        }

        &::after {
            top: vw(18);
            right: vw(18);
            bottom: vw(18);
            left: vw(18);
            clip-path: polygon($lineStart 0,
                    $lineEnd 0,
                    100% $lineStart,
                    100% $lineEnd,
                    $lineEnd 100%,
                    $lineStart 100%,
                    0 $lineEnd,
                    0 $lineStart,
                    $lineStart 0,

                    calc($lineStart + $lineW/2) $lineW,
                    $lineW calc($lineStart + $lineW/2),
                    $lineW calc($lineEnd - $lineW/2),
                    calc($lineStart + $lineW/2) calc(100% - $lineW),
                    calc($lineEnd - $lineW/2) calc(100% - $lineW),
                    calc(100% - $lineW) calc($lineEnd - $lineW/2),
                    calc(100% - $lineW) calc($lineStart + $lineW/2),
                    calc($lineEnd - $lineW/2) $lineW,
                    calc($lineStart + $lineW/2) $lineW);
        }

        &-head {
            padding: 0 vw(25);

            &>span {
                font-size: vw(72);
                letter-spacing: 0;
            }
        }

        &-content {
            padding: 0 vw(25);

            &::before {
                margin: vw(60) auto;
                width: vw(674);
                height: 1px;
            }
        }

        &-close {
            top: vw(50);
            right: vw(57);
            width: vw(120);
            height: vw(120);
        }
    }
}