.popup {
    position: fixed;
    top: 0;
    left: 0;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    padding: 0 120px;
    display: flex;
    flex-direction: column;
    align-items: center;
    opacity: 0;
    pointer-events: none;
    overflow: hidden;
    transition: opacity .3s;
    z-index: 100;

    &::before,
    &::after {
        content: '';
        display: block;
        height: 100%;
        min-height: 40px;
    }

    &.-show {
        opacity: 1;
        overflow-y: auto;
    }

    &.-show & {
        &-wrap {
            pointer-events: visible;
        }
    }

    &-wrap {
        position: relative;
        flex: 0 0 auto;
        box-sizing: border-box;
        width: 100%;
        background-color: #fff;
    }

    &-close {
        position: absolute;
        top: 0;
        right: 0;
        width: 100px;
        height: 100px;
        border-radius: 50%;
        // background: url(../image/icon/cross.svg) no-repeat center/100%;
    }

    @include breakpoint-down('tablet') {}
}

.login {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0);
    z-index: 105;
    pointer-events: none;
    transition: background-color .3s;

    &-wrap {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 60px;
        padding: 60px;
        border-radius: 50px 50px 0 0;
        background-color: rgba($color: #fff, $alpha: 0.9);
        transform: translateY(100%);
        transition: transform .3s;
    }

    .-loginOpen & {
        background-color: rgba(0, 0, 0, 0.5);
        pointer-events: visible;

        &-wrap {
            transform: translateY(0);
        }
    }

    @include breakpoint-down('tablet') {
        &-wrap {
            gap: vw(60);
            padding: vw(60);
            border-radius: vw(50) vw(50) 0 0;
        }
    }
}