  background: #ade2b0 url(../image/banner/pc.jpg) no-repeat center/contain;
}
body.-isPc > div:not(.-showPc) {
  display: none;
}
body.-isPopupOpen, body.-menuOpen {
  overflow: hidden;
}

.container {
  position: relative;
  box-sizing: border-box;
  max-width: 1170px;
  margin: 0 auto;
  background: url(../image/bg/bg.jpg) repeat-y top center/100%;
  overflow: hidden;
}
.container::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 562px;
  height: 251px;
  background: url(../image/bg/cloud.png) no-repeat center/contain;
  pointer-events: none;
}
.container.-index {
  background-image: url(../image/bg/index.jpg);
}
.container.-intro {
  background-image: url(../image/bg/index.jpg);
}
.container.-intro::before {
  content: none;
}
