    height: 21.452991453vw;
  }
}

.landscape {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(15px);
  z-index: 1000;
}
@media (orientation: landscape) {
  body:not(.-isPc) .landscape {
    display: flex;
  }
}

@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}
@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
.loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 9999;
}
.loading-wrap {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: inline-block;
  width: 80px;
  height: 80px;
}
.loading-dot {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #93bc58;
}
.loading-dot:nth-child(1) {
  left: 8px;
  animation: 0.6s infinite lds-ellipsis1;
}
.loading-dot:nth-child(2) {
  left: 32px;
  animation: 0.6s infinite lds-ellipsis2;
}
.loading-dot:nth-child(3) {
  left: 56px;
  animation: 0.6s infinite lds-ellipsis3;
}

/* ------------------------------- Header ------------------------------- */
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
}
.header-wrap {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1170px;
  margin: 0 auto;
  padding: 24px 48px;
}
.header-logo {
  flex: 0 0 auto;
  width: 216px;
  font-size: 0;
}
.header-logo img {
  width: 100%;
}
.-card .header-logo {
  opacity: 0;
  pointer-events: none;
}
.header-burger {
  flex: 0 0 auto;
  width: 144px;
  height: 144px;
  font-size: 0;
  background: url(../image/layout/menu.svg) no-repeat center/contain;
  box-shadow: 16px 15px 21px 0 rgba(4, 54, 1, 0.16);
