  transition: opacity 0.3s;
  z-index: 100;
}
.popup::before, .popup::after {
  content: "";
  display: block;
  height: 100%;
  min-height: 40px;
}
.popup.-show {
  opacity: 1;
  overflow-y: auto;
}
.popup.-show .popup-wrap {
  pointer-events: visible;
}
.popup-wrap {
  position: relative;
  flex: 0 0 auto;
  box-sizing: border-box;
  width: 100%;
  background-color: #fff;
}
.popup-close {
  position: absolute;
  top: 0;
  right: 0;
  width: 100px;
  height: 100px;
  border-radius: 50%;
}
.login {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0);
  z-index: 105;
  pointer-events: none;
  transition: background-color 0.3s;
}
.login-wrap {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 60px;
  padding: 60px;
  border-radius: 50px 50px 0 0;
  background-color: rgba(255, 255, 255, 0.9);
  transform: translateY(100%);
  transition: transform 0.3s;
}
.-loginOpen .login {
  background-color: rgba(0, 0, 0, 0.5);
  pointer-events: visible;
}
.-loginOpen .login-wrap {
  transform: translateY(0);
}
