// -------------------------------- scrollbar套件修改 start --------------------------------
.os-theme-dark {

    .os-scrollbar-horizontal {
        display: none;
    }

    &>.os-scrollbar {
        &>.os-scrollbar-track>.os-scrollbar-handle {
            background: #483629;
            cursor: pointer;

            &.active {
                background: #483629;
            }
        }

        &:hover>.os-scrollbar-track>.os-scrollbar-handle {
            background: #483629;
        }
    }

    &>.os-scrollbar-vertical {
        width: 12px;
        padding: 0;
        background: #e4e7e3;
        border-radius: 6px;
        z-index: 0;

        .os-scrollbar-track .os-scrollbar-handle {
            border-radius: 6px;
        }

        .os-scrollbar-handle:before {
            left: 0;
            right: 0;
        }
    }

    @include breakpoint-down('tablet') {
        &>.os-scrollbar-vertical {
            width: vw(12);
            border-radius: vw(6);

            .os-scrollbar-track .os-scrollbar-handle {
                border-radius: vw(6);
            }
        }
    }
}

// -------------------------------- scrollbar套件修改 end --------------------------------